@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500&display=swap");

.wp-block-guten-weather-guten-weather {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
	background-color: #fff;
	color: rgb(49, 49, 49);
	padding: 10px;
	border-radius: 4px;
	box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
	.weather-icon {
		width: 100px;
		height: 100px;
		margin-right: 10px;
	}
	.weather-text,
	.weather-loc-name,
	.weather-loc-coords,
	.weather-loc-timezone {
		font-family: "Open Sans", sans-serif;
	}

	.weather-text {
		font-size: 2.5rem;
		font-weight: 500;
		margin-bottom: 10px;
	}
}
